.box {
    position: relative;
    width: 100%;
    height: 25%;
}
.form {
    display: flex;
    position: absolute;
    left: 35%;
    top: 80%;
    width: 300px;
    height: 300px;
    justify-content: center;
    align-items: center;
    background-color: RGBA(0,72,123,0.76);
}
.formError {
    color: red;
    border: 2px solid red;
    max-width: 40%;
    min-width: relative;
    }
.form{
box-shadow: 10px 10px 38px 9px rgba(0,0,0,0.38);
-webkit-box-shadow: 10px 10px 38px 9px rgba(0,0,0,0.38);
-moz-box-shadow: 10px 10px 38px 9px rgba(0,0,0,0.38);
}
.form:hover {
    transform: scale(1.2) translate(-1px,-2px);
transition-duration: 0.3s;
}
.form form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.form form:last-child {
    flex-basis: 50px 50px;
}

